<template>
  <div class="welcome-container">
    <div class="welcome-detail">
      <div class="welcome-explanation">
        <p class="welcome-description caption-regular">
          {{ steps[currentStep].description[0] }}
        </p>
        <h2 class="welcome-title heading2-bold">
          {{ steps[currentStep].title }}
        </h2>
        <p class="welcome-description caption-regular">
          {{ steps[currentStep].description[1] }}
        </p>
        <p class="welcome-description caption-regular">
          {{ steps[currentStep].description[2] }}
        </p>
      </div>
      <Carousel :currentStep="currentStep" />
      <div class="welcome-image">
        <!-- <img :src="steps[currentStep].image" alt="Step image" class="step-image" /> -->
      </div>
    </div>

    <div class="next-button">
      <Button
        @click="nextStep"
        :text="buttonText"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import { Carousel, Button } from '@/components';
import { useRootStore } from '@/stores/rootStore';
import { storeToRefs } from 'pinia';
import { saveToLocalStorage } from '@/utils';

const router = useRouter();
const rootStore = useRootStore();
const { showUserGuide } = storeToRefs(rootStore);

const steps = [
  {
    title: '  ',
    description: [
      '  ',
      '  ',
      '   ',
    ],
    image: '',
  },
  {
    title: '  ',
    description: [
      '  ',
      ' ',
      '  ',
    ],
    image: '',
  },
  {
    title: '  ',
    description: [
      '    ',
      '  ',
      '   ',
    ],
    image: '',
  },
  {
    title: '   ',
    description: [
      '    ',
      '  ',
      '  ',
    ],
    image: '',
  },
];

const currentStep = ref(0);

const nextStep = () => {
  if (currentStep.value < steps.length - 1) {
    currentStep.value++;
  } else {
    showUserGuide.value = true;
    saveToLocalStorage('showUserGuide', 'true');
    router.push({ path: '/home' }); // Home   
  }
};

const buttonText = computed(() =>
  currentStep.value < steps.length - 1 ? '' : ''
);
</script>

<style scoped>
.welcome-container {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: var(--color-background);
  max-width: 47.2rem;
  justify-content: space-between;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.welcome-detail {
  width: 100%;
  text-align: center;
  flex-direction: column;
  display: flex;
  align-items: center;
  gap: var(--spacing-8);
  padding: 20px;
  flex: 1;
  justify-content: center;
}

.welcome-explanation {
  flex-direction: column;
  display: flex;
}

.welcome-description {
  color: var(--color-neutral-500);
}

.welcome-title {
  color: var(--color-neutral-100);
}

.welcome-image {
  width: 164px;
  height: 328px;
  background-color: #fff;
  border-radius: 20px;
}

.next-button {
  align-self: center;
  padding: 20px;
  width: 100%;
}
</style>
